.slider-container {
  margin-bottom: 50px;
}

.slider {
  width: 100%;
}

.range-slider {
  -webkit-appearance: none;
  width: 70%;
  height: 10px;
  border-radius: 5px;
  background: #e7e7e7;
  outline: none;
  padding: 0;
  margin: 0;
}

@media screen and (min-width: 768px) {
  .range-slider {
    width: 85%;
  }
}

.range-slider::-webkit-slider-thumb {
  appearance: none;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #2563eb;
  cursor: pointer;
  transition: background 0.15s ease-in-out;
}

.range-slider:active::-webkit-slider-thumb {
  background: #2563eb;
}

.range-slider::-moz-range-thumb {
  width: 20px;
  height: 20px;
  border: 0;
  border-radius: 50%;
  background: red;
  cursor: pointer;
  transition: background 0.15s ease-in-out;
}

::-moz-range-track {
  background: #fff;
  border: 0;
}

.range-slider-value {
  display: inline-block;
  position: relative;
  width: 60px;
  color: #fff;
  line-height: 20px;
  text-align: center;
  border-radius: 3px;
  background: #2563eb;
  padding: 5px 10px;
  margin-left: 12px;
}

.range-slider-value:after {
  position: absolute;
  top: 8px;
  left: -7px;
  width: 0;
  height: 0;
  border-top: 7px solid transparent;
  border-right: 7px solid #2563eb;
  border-bottom: 7px solid transparent;
  content: "";
}
